/* Scoped styles for Route Management page */
:root{
  --primary: #2563eb;
  --muted: #6b7280;
  --bg: #f8fafc;
  --card-bg: #ffffff;
  --border: #e6eefc;
  --success: #10b981;
  --warning: #f59e0b;
  --danger: #ef4444;
}

/* main container */
.container-route-management{
  width: 100%;
  margin: 20px auto;
  padding: 18px;
  box-sizing: border-box;
  font-family: Inter, "Segoe UI", Roboto, system-ui, -apple-system, "Helvetica Neue", Arial;
}

/* header */
.container-route-management header{
  margin-bottom: 14px;
}
.container-route-management h1{
  display:flex;
  align-items:center;
  gap:10px;
  font-size:1.4rem;
  margin:0 0 6px 0;
  color:var(--primary);
}
.container-route-management .description{
  margin:0;
  color:var(--muted);
  font-size:0.95rem;
}

/* dashboard cards */
.dashboard{
  display:grid;
  grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
  gap:12px;
  margin: 14px 0;
}
.dashboard .card{
  background: linear-gradient(180deg, rgba(255,255,255,0.9), rgba(250,250,255,0.95));
  border-radius:12px;
  padding:14px;
  box-shadow: 0 6px 18px rgba(2,6,23,0.06);
  display:flex;
  flex-direction:column;
  align-items:flex-start;
  gap:8px;
}
.dashboard .card i { font-size:20px; color:var(--primary); background:rgba(37,99,235,0.08); padding:8px; border-radius:8px; }
.dashboard .card h3 { margin:0; font-size:0.95rem; color:#111827; font-weight:600; }
.dashboard .card .number { font-size:1.25rem; font-weight:700; color:#0f172a; }

/* controls */
.controls{
  display:flex;
  gap:12px;
  align-items:center;
  margin-bottom:12px;
  flex-wrap:wrap;
}
.search-container{
  display:flex;
  align-items:center;
  gap:8px;
  background:var(--card-bg);
  border:1px solid var(--border);
  padding:6px 10px;
  border-radius:10px;
  flex:1 1 320px;
  min-width:0;
}
.search-box{
  border:0;
  outline:none;
  width:100%;
  font-size:0.95rem;
  background:transparent;
}
.filter-container{
  display:flex;
  align-items:center;
  gap:8px;
  background:var(--card-bg);
  border:1px solid var(--border);
  padding:6px 10px;
  border-radius:10px;
}
.filter-select{
  border:0;
  background:transparent;
  outline:none;
  font-size:0.95rem;
}

/* table */
.table-container{
  background:var(--card-bg);
  border-radius:12px;
  padding:8px;
  box-shadow: 0 8px 30px rgba(2,6,23,0.04);
  overflow:hidden;
}
.routes-table{
  width:100%;
  border-collapse:collapse;
  min-width:800px; /* allow horizontal scroll on small screens */
}
.routes-table thead th{
  text-align:left;
  padding:12px 14px;
  font-weight:700;
  color:#0f172a;
  background: linear-gradient(180deg, rgba(245,248,255,1), rgba(240,246,255,1));
  border-bottom: 2px solid var(--border);
  font-size:0.9rem;
}
.routes-table tbody td{
  padding:12px 14px;
  border-bottom:1px solid var(--border);
  vertical-align:middle;
  font-size:0.95rem;
  color:#111827;
}
.routes-table tbody tr:hover td{
  background: #fbfdff;
}

/* statuses & small UI */
.status{ display:inline-flex; gap:8px; align-items:center; padding:6px 10px; border-radius:999px; font-weight:600; font-size:0.9rem; }
.status-stable{ color:var(--success); background: rgba(16,185,129,0.08); }
.status-warning{ color:var(--warning); background: rgba(245,158,11,0.08); }
.status-danger{ color:var(--danger); background: rgba(239,68,68,0.08); }

.no-vehicle{ color:#b91c1c; font-size:0.85rem; margin-top:6px; display:flex; gap:6px; align-items:center; }

.btn-detail{
  background:var(--primary);
  color:white;
  border:none;
  padding:8px 12px;
  border-radius:8px;
  cursor:pointer;
  font-weight:600;
  transition:transform .12s ease, box-shadow .12s ease;
}
.btn-detail:hover{ transform:translateY(-3px); box-shadow: 0 10px 30px rgba(37,99,235,0.12); }

/* responsive */
@media (max-width: 900px){
  .routes-table{ min-width:720px; }
}
@media (max-width: 600px){
  .dashboard{ grid-template-columns: 1fr 1fr; }
  .routes-table{ min-width:560px; }
  .controls{ flex-direction:column; align-items:stretch; }
}
